import React, { useState } from "react";

import MainLayout from "../layouts/MainLayout";
import { guess } from "../bao-thach";

class NHTPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      q1: 0,
      q2: 0,
      q3: 0,
      q4: 0,
      q5: 0,
      q6: 0,
      q7: 0,
      q8: 0,
      busy: false,
      result: null
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleSubmit(event) {
    event.preventDefault();

    this.setState({
      busy: true
    });

    var result = {};
    for (var i = 1; i <= 8; i++) {
      const t = guess({
        q1: parseInt(this.state.q1) || 0,
        q2: parseInt(this.state.q2) || 0,
        q3: parseInt(this.state.q3) || 0,
        q4: parseInt(this.state.q4) || 0,
        q5: parseInt(this.state.q5) || 0,
        q6: parseInt(this.state.q6) || 0,
        q7: parseInt(this.state.q7) || 0,
        q8: parseInt(this.state.q8) || 0,
        targetLevel: i
      });

      if (t[i]) result[i] = t;
    }

    this.setState({
      result: result,
      busy: false
    });
  }

  render() {
    const { q1, q2, q3, q4, q5, q6, q7, q8, busy, result } = this.state;
3
    const results = result
      ? Object.keys(result).map(level => {
          const resultByLevel = result[level];
          return Object.keys(resultByLevel)
            .filter(nestedLevel => resultByLevel[nestedLevel] > 0)
            .map(
              nestedLevel => `${resultByLevel[nestedLevel]} C${nestedLevel}`
            )
            .join(", ");
        })
      : null;

    return (
      <MainLayout>
        <h2>Bàn tính Bảo Thạch</h2>

        <h3>Thông tin cơ bản</h3>
        <ul>
          <li>1 C2 = 5 C1</li>
          <li>1 C3 = 5 C2</li>
          <li>1 C4 = 5 C3</li>
          <li>1 C5 = 5 C4</li>
          <li>1 C6 = 5 C5</li>
          <li>1 C7 = 5 C6</li>
          <li>1 C8 = 4 C7</li>
        </ul>

        <ul>
          <li>Nhập số lượng bảo thạch theo cấp mà bạn chuẩn bị</li>
          <li>Tool sẽ tính được số ngọc tối đa theo cấp có thể đạt được</li>
        </ul>
        

        <form onSubmit={this.handleSubmit}>
          <div className="form-row">
            <div className="col-12 col-md">
              <label htmlFor="q1">C1</label>
              <input
                name="q1"
                type="number"
                placeholder="C1"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q1}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q2">C2</label>
              <input
                name="q2"
                type="number"
                placeholder="C2"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q2}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q3">C3</label>
              <input
                name="q3"
                type="number"
                placeholder="C3"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q3}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q4">C4</label>
              <input
                name="q4"
                type="number"
                placeholder="C4"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q4}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q5">C5</label>
              <input
                name="q5"
                type="number"
                placeholder="C5"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q5}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q6">C6</label>
              <input
                name="q6"
                type="number"
                placeholder="C6"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q6}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q7">C7</label>
              <input
                name="q7"
                type="number"
                placeholder="C7"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q7}
              />
            </div>

            <div className="col-12 col-md">
              <label htmlFor="q8">C8</label>
              <input
                name="q8"
                type="number"
                placeholder="C8"
                className="form-control"
                onChange={this.handleInputChange}
                value={this.state.q8}
              />
            </div>
          </div>

          <div className="mt-3">
            <input
              type="submit"
              value="Submit"
              className="btn btn-success"
              disabled={busy}
            />
          </div>
        </form>

        {result && (
          <table className="mt-4 table table-bordered table-striped">
            <thead>
              <tr>
                <th>Cấp mục tiêu\Số lượng đầu cuối</th>
                <th>1</th>
                <th>2</th>
                <th>3</th>
                <th>4</th>
                <th>5</th>
                <th>6</th>
                <th>7</th>
                <th>8</th>
              </tr>
            </thead>

            <tbody>
              {Object.keys(result).map(targetLevel => (
                <tr key={`tl${targetLevel}`}>
                  <td className="font-weight-bold">{targetLevel}</td>
                  {Object.keys(result[targetLevel]).map(nestedLevel => (
                    <td
                      key={`tl-${targetLevel}-${nestedLevel}`}
                      className={
                        result[targetLevel][nestedLevel]
                          ? "font-weight-bold"
                          : ""
                      }
                    >
                      {result[targetLevel][nestedLevel]}
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
        )}

        {results && results.length > 0 && (
          <div>
            <ul>
              {results.map((line, idx) => (
                <li key={idx}>{line}</li>
              ))}
            </ul>
          </div>
        )}
      </MainLayout>
    );
  }
}

export default NHTPage;
